@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@mixin title {
  margin: 0;
  padding: 0;
  text-align: center;
}

@mixin oswald400 {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}

@mixin lato400 {
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}
