.header {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  background-color: #ffffff;
  text-transform: uppercase;
}

.header__wrapper {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  width: 280px;
  padding-top: 10px;
  padding-bottom: 18.39px;
}

.header__toggle {
  width: 24px;
  height: 18px;
  border: 0;
  color: #000000;
  background: url("../img/logo_hamburger.svg");
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
